<script>
  import { fade, scale } from "svelte/transition";
  import collection from "../data/collection.json";
  import equipment from "../data/equipment.json";
  import souvenir from "../data/souvenir.json";
  import spellcard from "../data/spellcard.json";
  import role from "../data/role.json";
  import story from "../data/story.json";
  import { Explain } from "../addon/base";

  let index = "collection";
  let selected = null;
  const info = {
    collection,
    equipment,
    souvenir,
    spellcard,
    role,
  };
  function Index(i) {
    selected = null;
    index = i;
  }
</script>

<div class="body" in:fade={{ duration: 250 }} out:fade={{ duration: 250 }}>
  <div class="index">
    <txt
      class={index == "collection" && "selected"}
      on:click={(_) => Index("collection")}>收藏品</txt
    >
    <txt
      class={index == "souvenir" && "selected"}
      on:click={(_) => Index("souvenir")}>纪念品</txt
    >
    <txt
      class={index == "equipment" && "selected"}
      on:click={(_) => Index("equipment")}>装备</txt
    >
    <txt>符卡</txt>
    <txt>消耗品</txt>
    <txt>角色</txt>
  </div>
  <div class="main">
    {#if index == "collection"}
      <div
        class="collections"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each Object.keys(collection.sort) as s}
          {#each collection.sort[s] as c}
            <div
              class={selected == c && "selected"}
              on:click={(_) => (selected = c)}
            >
              <img src="/svg/collection/{c}.svg" />
              <Explain
                title={collection[c].name}
                detail={collection[c].detail}
                color={s == "white" ? "blue" : s}
              />
            </div>
          {/each}
        {/each}
      </div>
    {/if}
    {#if index == "souvenir"}
      <div
        class="souvenirs"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each souvenir.sort as s}
          <div
            class={selected == s && "selected"}
            on:click={(_) => (selected = s)}
          >
            <img src="/svg/souvenir/{s}.svg" />
            <Explain
              title={souvenir[s].name}
              detail={souvenir[s].detail}
              color="purple"
            />
          </div>
        {/each}
      </div>
    {/if}
    {#if index == "equipment"}
      <div
        class="equipments"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        {#each equipment.sort as s}
          <div
            class={selected == s && "selected"}
            on:click={(_) => (selected = s)}
          >
            <img src="/svg/equipment/{s}.svg" />
            <Explain
              title={equipment[s].name}
              detail={equipment[s].detail}
              color="gold"
            />
          </div>
        {/each}
      </div>
    {/if}
    {#if selected}
      <div
        class="detail"
        in:fade={{ duration: 200 }}
        out:fade={{ duration: 200 }}
      >
        <img src="/svg/{index}/{selected}.svg" />
        <txt class="name">{info[index][selected].name}</txt>
        <txt class="effect">{info[index][selected].detail}</txt>
        <txt class="story">{story[index][selected]}</txt>
      </div>
    {/if}
  </div>
</div>

<style>
  .body {
    background-image: url("/img/scene/afflatus.webp");
    background-position: center bottom;
  }

  .index {
    width: 900px;
    flex-direction: row;
  }

  .index > txt {
    width: 140px;
    line-height: 40px;
    margin: 5px;
    background-color: #63636369;
    border: 3px solid #ddd;
    font-size: 20px;
    color: white;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
    font-family: SanJiSuXianJianTi;
    transition: 0.2s;
  }

  .index > txt.selected,
  .index > txt:hover {
    background-color: #a1a1a169;
    border-color: white;
    transform: scale(1.05);
  }

  .main {
    margin-top: 5px;
    height: 480px;
    width: 900px;
    flex-direction: row;
    position: relative;
  }

  .equipments,
  .souvenirs,
  .collections {
    flex-direction: row;
    flex-wrap: wrap;
    width: 600px;
    height: 480px;
    align-content: flex-start;
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
  }

  .main > div > div {
    margin: 5px;
    background-color: rgba(0, 0, 0, 0.3);
    box-sizing: border-box;
    cursor: pointer;
    transition: 0.2s;
  }

  .main > div > div.selected,
  .main > div > div:hover {
    border-color: white;
    transform: scale(1.1);
  }

  .main > div > div > img {
    margin: auto;
    width: 75%;
    height: 75%;
  }

  .collections > div {
    width: 50px;
    height: 50px;
    border: 3px solid #aaa;
  }

  .equipments > div,
  .souvenirs > div {
    width: 90px;
    height: 90px;
    border: 4px solid #aaa;
  }

  .detail {
    position: absolute;
    right: 0;
    top: 0;
    width: 290px;
    height: 480px;
    background-color: rgba(102, 102, 102, 0.3);
    backdrop-filter: blur(16px);
    box-sizing: border-box;
    border: 4px solid #aaa;
    margin: 5px;
    align-items: center;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* IE 10+ */
  }

  .detail::-webkit-scrollbar {
    display: none; /* Chrome Safari */
  }

  .detail > img {
    width: 100px;
    padding: 10px 20px;
    border-bottom: 2px solid #a5a5a5;
  }

  .detail > .name {
    font-size: 24px;
    color: white;
    margin: 10px;
    font-family: TiejiliSC;
  }

  .detail > .effect {
    width: 240px;
    text-align: center;
    font-size: 16px;
    color: white;
    font-family: seto;
  }

  .detail > .story {
    font-family: SanJiSuXianJianTi;
    width: 240px;
    padding: 20px;
    font-size: 16px;
    color: #dedede;
    white-space: pre-wrap;
  }
</style>
