<script>
  import { fade, scale } from "svelte/transition";
  import { page, data, explore } from "../store";
  import Selector from "../addon/selector.svelte";
  import spellcard from "../data/spellcard.json";
  import enermy from "../data/enermy.json";
  import growth from "../data/growth";

  let selected = false;
  let step = 0;
  let pointer = 0;
  let type = null;
  let click = true;
  let story = [
    {
      type: "txt",
      value: "宇佐见莲子正对着笔记本电脑沉思。",
    },
    {
      type: "txt",
      value: "“终于要动笔了吗？”梅莉端着咖啡走来。",
    },
    {
      type: "txt",
      value: "“是啊。”莲子伸了个懒腰。",
    },
    {
      type: "txt",
      value: "“要加多少糖呢？”",
    },
    {
      type: "selector",
      value: "sugar",
    },
    {
      type: "txt",
      value: "“幻想交织在静谧的夜空之下——”莲子写道。",
    },
    {
      type: "txt",
      value: "“这句话有什么作用吗？”",
    },
    {
      type: "txt",
      value: "“文、文学的事情，你少管！”",
    },
    {
      type: "txt",
      value: "“少女坐在破旧的神社中。”她接着写，“其名为——”",
    },
    {
      type: "selector",
      value: "role",
    },
  ];

  function next() {
    if (story[step].type == "txt") step++;
    else if (selected) {
      click = false;
      setTimeout((_) => {
        if (step == story.length - 1) start();
        else {
          pointer = step + 1;
          step += 1;
          type = null;
          selected = false;
          click = true;
          setTimeout((_) => next(), 200);
        }
      }, 1000);
    } else type = story[step].value;
  }

  next();

  function start() {
    const add = {
      enermy: (type) =>
        $explore.enermy.push(enermy.sort[$data.scene][type].rd()[0]),
    };
    add.enermy("normal");
    let amount = growth.amount($explore.lv);
    for (let i = 0; i < amount + 10; i++) add.enermy("normal");
    $data.spellcards[$data.role] = 4;
    spellcardAdd();
    spellcardAdd();
    $page = "Explore";
  }

  function spellcardAdd() {
    let name = deepCopy(spellcard).sort[$data.role].rd()[0];
    if ($data.spellcards[name]) $data.spellcards[name]++;
    else $data.spellcards[name] = 1;
  }
</script>

<div
  class="body"
  in:fade={{ duration: 250 }}
  out:fade={{ duration: 250 }}
  on:click={(_) => click && next()}
>
  <div class="talk">
    {#each story.slice(pointer, step) as s, i}
      <txt
        class={i + pointer == step - 1 && "reading"}
        style="--length:{s.value.getByteLen() / 2 -
          1};width:{s.value.getByteLen() - 2}ch;--time:{i + pointer == step - 1
          ? 0.2 * (s.value.getByteLen() / 2 - 1)
          : 0}s">{s.value}</txt
      >
    {/each}
  </div>
  {#if type}
    <Selector {type} bind:selected />
  {/if}
</div>

<style>
  .body {
    background-image: url("/img/scene/foreword.webp");
  }

  .talk {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }

  .talk > txt {
    margin: 5px;
    font-size: 20px;
    font-family: SanJiSuXianJianTi;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
    white-space: nowrap;
    overflow: hidden;
    animation: typing var(--time) steps(var(--length), end),
      cursor-blink 0.3s step-end infinite alternate;
  }

  .reading {
    border-right: 0.1em solid;
  }

  @keyframes typing {
    from {
      width: 0;
    }
  }

  @keyframes cursor-blink {
    50% {
      border-color: transparent;
    }
  }
</style>
