<script>
  import { beforeUpdate, afterUpdate } from "svelte";
  import { fade, scale } from "svelte/transition";
  import { data, cache, setting } from "../store";
  import Spellcard from "./battleSpellcard.svelte";
  import Enermy from "./battleEnermy.svelte";
  import State from "./battleState.svelte";
  import Record from "./battleRecord.svelte";
  import buffs from "../data/buff.json";
  import equipment from "../data/equipment.json";

  const Data = deepCopy($data);
  const raf = new window.RAF();
  let used;
  let Selected;
  let refresh;
  let ordered;
  let round = true;
  let Power;
  let role;
  let enermy;
  let loaded = false;
  let equipmentLimit = 1;
  let record;
  let result;

  afterUpdate((_) => {
    if (!loaded) {
      refresh(speedHandle());
      loaded = true;
    }
  });
  C("philosopher's_stone", (amount) => (equipmentLimit += amount));

  function aimed(index) {
    let cost = Selected == Data.role ? 1 : 3;
    E("mirror_of_pear", (_) => (cost = Selected == Data.role ? 0 : cost));
    S("mimicry_cat_pendant", (_) => {
      role.state({
        Health: role.state().Health * (1 - 0.1 * cost),
      });
      cost = 0;
    });
    if (!round) msg({ content: "不在你的回合" });
    else if (cost > Power.get()) msg({ content: "灵力不足" });
    else {
      used();
      Power.cost(cost);
      let RoleInput = enermy.input(role.output(Selected), index);
      role.input(RoleInput, index);
      Selected = null;
    }
  }
  function switchRound() {
    switchEvent();
    // 续个回合
    round = false;
    record.clear();
    const unit = 200,
      afterEnd = 2,
      beforeStart = 4,
      enermyInterval = 6,
      spellcardInterval = 6,
      afterSwitch = 2,
      originOutput = enermy.output(),
      EnermyOutput = deepCopy(originOutput);
    const process = [
      {
        handle: false,
        time: afterEnd,
      },
    ];
    let step = raf.setInterval((_) => {
      process[0].time -= 1;
      if (process[0].time == 0) {
        process[0].handle && process[0].handle();
        const target = EnermyOutput.filter((e) => e),
          index = EnermyOutput.length - target.length;
        if (result) raf.clearInterval(step);
        else if (target.length == 0) {
          raf.clearInterval(step);
          raf.setTimeout((_) => {
            ordered = null;
            round = true;
          }, afterSwitch * unit);
          raf.setTimeout((_) => {
            role.round(roundEvent);
            refresh(speedHandle());
          }, beforeStart * unit);
        } else if (target[0].length == 0)
          process.push({
            handle: (_) => (EnermyOutput[index] = false),
            time: enermyInterval,
          });
        else {
          if (target[0].length == originOutput[index].length)
            raf.setTimeout((_) => {
              ordered = index;
              enermy.round(roundEvent, index);
            }, afterSwitch * unit);
          process.push({
            handle: (_) => {
              if (enermy.state()[index].Health <= 0)
                EnermyOutput[index] = false;
              else {
                enermy.input(role.input(target[0][0].output, index), index);
                record.add({
                  enermy: index,
                  spellcard: target[0][0].spellcard,
                });
                target[0].shift();
              }
            },
            time: enermy.state()[index].Health > 0 ? spellcardInterval : 1,
          });
        }
        process.shift();
      }
    }, unit);
  }
  function switchEvent() {
    let input = {
      buff: [],
      attack: [],
      heal: [],
    };
    C("ghost_lantern", (amount) =>
      input.buff.push({
        value: "interim_barrier",
        amount: Math.ceil(role.state().health * 0.05 * amount) * Power.get(),
      })
    );
    role.input(input);
  }
  function roundEvent(Datas) {
    const input = {
      heal: [],
      attack: [],
      buff: [],
    };
    let p = Datas.state.power;
    let hypodynamic;
    B(Datas, "in_treatment", (amount) =>
      input.heal.push({
        type: "Rate",
        value: 0.05 * amount,
      })
    );
    B(Datas, "burning", (amount) =>
      input.attack.push({
        value: 0.02 * amount,
        type: "rate",
        indirect: true,
        critical: false,
        aim: "one",
      })
    );
    B(Datas, "bleed", (amount) =>
      input.attack.push({
        value: 0.02 * amount,
        type: "rate",
        indirect: true,
        critical: false,
        aim: "one",
      })
    );
    B(Datas, "hypodynamic", (_) => (hypodynamic = 1));
    B(Datas, "star", (_) => {
      let target = $cache["bottle_of_stars"].filter(
        (s) => s.index == Datas.index
      );
      target.forEach((t) =>
        input.attack.push({
          type: "real",
          value: t.damage,
          aim: "one",
          from: "role",
          indirect: true,
          critical: false,
        })
      );
      $cache["bottle_of_stars"] = $cache["bottle_of_stars"].filter(
        (s) => s.index != Datas.index
      );
    });
    if (input.heal.length > 0 || input.attack.length > 0) Datas.input(input);
    for (let b in Datas.buff) buffs[b].interim && delete Datas.buff[b];
    !hypodynamic && Datas.power(p);
    !hypodynamic && p && Data.role == "marisa" && Power.add(5);
    S("wanderlust", (_) => p && Power.add(Math.floor(role.state().speed)));
  }
  function killEvent(index) {
    const output = {
      attack: [],
      heal: [],
      buff: [],
    };
    C("bird_wine", (amount) =>
      output.buff.push({
        value: "burning",
        amount: amount,
        aim: "all",
      })
    );
    enermy.input(output);
    C("crown_of_thorns", (amount) => Power.add(amount));
    if (enermy.state().filter((e) => e.Health > 0).length <= 1)
      raf.setTimeout((_) => {
        msg({ content: "胜利" });
      }, 1200);
  }
  function deadEvent(k) {
    msg({ content: "失败" });
    result = "dead";
  }
  function critical(from) {
    let base = 0.05;
    if (from == "role") {
      let state = role.state();
      C(
        "burning_seashore_flower",
        (amount) =>
          (base +=
            Math.floor(((state.health - state.Health) / state.health) * 100) *
            (0.005 + (amount - 1) * 0.0025))
      );
      C("fluorescent_zanthoxylum", (amount) => (base += amount * 0.05));
      if (Data.role == "youmu") base = 0;
    }
    return r() < base;
  }
  function speedHandle() {
    let target = enermy.state().filter((e) => e.Health > 0);
    let adv = target.reduce((a, c) => a + c.speed, 0) / target.length;
    let value = Math.abs(role.state().speed - adv);
    let n = value - parseInt(value);
    let res = parseInt(value) + Number(r() < n);
    return role.state().speed > adv ? res : -res;
  }
  function Equipment() {
    if (equipmentLimit > 0) {
      const output = {
        attack: [],
        buff: [],
        heal: [],
      };
      let u = true;
      E("mini_bagua_stove", (_) => {
        output.attack.push({
          value: 400,
          type: "scale",
          aim: "all",
        });
      });
      E("louguanjian", (_) => {
        output.attack.push({
          value: 0.2,
          type: "Rate",
          aim: "all",
        });
      });
      E("hd_camera", (_) => {
        enermy.state().map((_, i) => enermy.buff(true, i));
        $cache["bottle_of_stars"] = [];
      });
      E("sunshade", (_) => {
        let v = role.state().Health - role.state().health * 0.3;
        if (v > 0) {
          role.state({
            Health: v,
          });
          output.buff.push({
            value: "barrier",
            amount: Math.ceil(role.state().health * 0.6),
            aim: "self",
          });
        } else {
          msg({ content: "不满足使用条件" });
          u = false;
        }
      });
      role.input(enermy.input(output));
      u && equipmentLimit--;
    } else msg({ content: "可使用次数已用尽" });
  }
  function B(b, n, f) {
    if (n in b.buff) {
      let a = b.buff[n];
      b.role &&
        !buffs[n].positive &&
        C("fish_stone", (amount) => (a -= amount));
      f(a >= 0 ? a : 0);
      if (buffs[n].expend) b.buff[n] == 1 ? delete b.buff[n] : b.buff[n]--;
      if (n == "star") delete b.buff[n];
    }
  }
  function C(n, f) {
    if (n in Data.collections) f(Data.collections[n]);
  }
  function E(n, f) {
    if (Data.equipment == n) f();
  }
  function S(n, f) {
    if (Data.souvenir == n) f();
    return Data.souvenir == n;
  }
</script>

<div
  class="body {Data.role} {Selected} {result}"
  style="background-image:url('/img/scene/{Data.scene}.webp')"
  in:fade={{ duration: 250 }}
  out:fade={{ duration: 250 }}
>
  <div class="battle">
    <Enermy
      {Selected}
      {aimed}
      {ordered}
      {critical}
      {killEvent}
      {role}
      bind:enermy
    />
    <Record bind:record />
    <Spellcard bind:used bind:Selected bind:refresh />
    <State {critical} {killEvent} {deadEvent} {enermy} bind:Power bind:role />
    <div class="action">
      <div class="consumables">
        <img src="/svg/consumables.svg" />
        <txt>消耗品</txt>
      </div>
      <div class="back" on:click={(_) => null}>
        <img src="/svg/back.svg" />
        <txt>回溯</txt>
      </div>
      <div class="equipment" on:click={(_) => Equipment()}>
        <img
          src="/svg/equipment/{Data.equipment ? Data.equipment : 'empty'}.svg"
        />
        <txt>{Data.equipment ? equipment[Data.equipment].name : "空"}</txt>
      </div>
      <div
        class={`end ${!round && "outRound"}`}
        on:click={(_) => round && switchRound()}
      >
        <txt>结束回合</txt>
      </div>
    </div>
  </div>
  <div class="barrier body" style="z-index:{result ? 5 : -1}">

    <div></div>
  </div>
</div>

<style>
  .battle {
    justify-content: space-between;
    align-items: center;
    height: 640px;
  }

  :global(.battle-input) {
    position: absolute;
    color: white;
    font-size: 24px;
    animation: battleDamage 0.8s forwards;
    text-shadow: 0 0 3px #666, 0 0 3px #666, 0 0 3px #666, 0 0 3px #666,
      0 0 3px #666, 0 0 3px #666, 0 0 3px #666;
    z-index: 2;
  }

  :global(.battle-heal) {
    position: absolute;
    color: lime;
    font-size: 24px;
    animation: battleHeal 0.8s forwards;
    text-shadow: 0 0 2px white, 0 0 2px white, 0 0 2px white, 0 0 2px white,
      0 0 2px white, 0 0 2px white, 0 0 2px white;
    z-index: 2;
  }

  :global(.critical) {
    text-shadow: 0 0 3px red, 0 0 3px red, 0 0 3px red, 0 0 3px red, 0 0 3px red,
      0 0 3px red, 0 0 3px red;
  }

  .action {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 200px;
    height: 200px;
    border-radius: 200px 0 0 0;
    background-color: rgb(239 140 140);
    backdrop-filter: blur(8px);
  }

  .back,
  .equipment,
  .consumables {
    position: absolute;
    align-items: center;
    cursor: pointer;
    filter: brightness(0.9);
    opacity: 0.8;
    transition: 0.2s;
  }

  .back:hover,
  .equipment:hover,
  .consumables:hover {
    filter: brightness(1);
    opacity: 1;
  }

  .back > img,
  .equipment > img,
  .consumables > img {
    width: 54px;
  }

  .back > txt,
  .equipment > txt,
  .consumables > txt {
    font-size: 16px;
    color: white;
    font-weight: bold;
    font-family: SanJiSuXianJianTi;
  }

  .consumables {
    right: 10px;
    top: 10px;
  }

  .back {
    left: 75px;
    top: 50px;
  }

  .equipment {
    bottom: 10px;
    left: 15px;
  }

  .end {
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: white;
    background-image: url("/svg/outRound.svg");
    background-size: 60px;
    background-repeat: no-repeat;
    background-position: 100px 100px;
    border-radius: 100px 0 0 0;
    height: 100px;
    width: 100px;
    transition: 0.3s;
    cursor: pointer;
    overflow: hidden;
  }

  .end > txt {
    font-size: 24px;
    color: #777;
    position: absolute;
    right: 5px;
    bottom: 8px;
    width: 60px;
    letter-spacing: 5px;
    transition: 0.3s;
    font-family: TiejiliSC;
    line-height: 28px;
  }

  .end:hover {
    background-color: #eee;
  }

  .outRound {
    background-position: 30px 30px;
  }

  .outRound > txt {
    transform: translate3d(-70px, -70px, 0);
  }

  .dead {
    animation: dead 4s forwards;
  }

  @keyframes battleDamage {
    0% {
      opacity: 0;
      transform: scale(0.6);
    }

    30% {
      opacity: 1;
      transform: scale(1.4);
    }

    40% {
      transform: scale(1);
    }

    90% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }

  @keyframes battleHeal {
    0% {
      opacity: 0;
      transform: scale(0.6);
    }

    20% {
      opacity: 1;
      transform: scale(1);
    }

    50% {
      opacity: 1;
      transform: translateY(0px);
    }

    100% {
      opacity: 0;
      transform: translateY(-30px);
    }
  }

  @keyframes dead {
    100% {
      filter: saturate(0) blur(4px);
    }
  }
</style>
