<script>
  import { data, setting } from "../store";
  import sugar from "../data/sugar.json";
  import role from "../data/role.json";

  export let type;
  export let selected;
  const info = {
    sugar,
    role,
  };
  let selections = deepCopy(info[type]).sort.rd().slice(0, $data.sugar);
  const icon = (type, value) => {
    return {
      role: `/img/role/${$setting.resource}/${value}.webp`,
      sugar: `/svg/sugar.svg`,
    }[type];
  };
</script>

<div class="selections">
  {#each selections as value, i (i)}
    <div
      class="selection animation_{selected ? 'close' : 'open'}Selections"
      on:click={(_) => {
        selected = true;
        let v = type == "sugar" ? Number(value.slice(1)) : value;
        if (["equipment", "role", "sugar"].includes(type)) $data[type] = v;
        else {
          if (value in $data[type]) $data[type][v]++;
          else $data[type][v] = 1;
        }
      }}
    >
      <img src={icon(type, value)} />
      <a>{info[type][value].title}</a>
      <div>
        <a>{info[type][value].detail}</a>
      </div>
    </div>
  {/each}
</div>

<style>
  .selections {
    flex-direction: row;
    justify-content: center;
  }

  .selection {
    width: 120px;
    height: 180px;
    border-radius: 12px;
    border: 4px solid #ad4234;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: 0.4s;
    filter: drop-shadow(0 0 6px #666);
    cursor: pointer;
    margin: 0px 10px;
  }

  .selection:hover {
    transform: scale(1.1);
    filter: drop-shadow(0 0 6px #fff);
  }

  .selection > img {
    height: 80px;
    margin-top: 5px;
    padding: 5px 10px;
    border-bottom: 2px solid #aaa;
  }

  .selection > a {
    font-size: 18px;
    color: #883429;
    font-weight: bold;
    margin-top: 2px;
    font-family: SanJiSuXianJianTi;
  }

  .selection > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 48px;
  }

  .selection > div > a {
    font-size: 12px;
    color: #666;
    width: 84px;
    text-align: center;
    font-family: seto;
  }

  .animation_openSelections {
    animation: openSelections 0.4s;
  }

  .animation_closeSelections {
    animation: closeSelections 0.15s forwards;
  }

  @keyframes openSelections {
    0% {
      opacity: 0;
      transform: scale(0.6);
    }

    80% {
      opacity: 1;
      transform: scale(1.1);
    }

    100% {
      transform: scale(1);
    }
  }

  @keyframes closeSelections {
    0% {
      transform: scale(1);
      opacity: 1;
    }

    100% {
      transform: scale(0.6);
      opacity: 0;
    }
  }
</style>
