<script>
  import info from "../data/spellcard.json";
  import { data } from "../store";

  const Data = deepCopy($data);
  const limit = S("wanderlust", _ => null) ? 3 : 4;
  const spellcards = [];
  for (let s in Data.spellcards)
    for (let i = 0; i < Data.spellcards[s]; i++) spellcards.push(s);
  C("blade_of_yellow_spring", (amount) =>
    [...Array(amount).keys()].forEach((_) => spellcards.push(Data.role))
  );
  let left = leftReset();
  let hold = [];
  let selected;
  let useds = [];

  export let Selected;
  export const used = (_) => (useds = [...useds, selected]);
  export const refresh = add;

  $: useThis = (i) => useds.includes(i);

  function add(value) {
    // 修改后的机制，用索引代表符卡，避免普攻被吞
    let l = limit + value;
    S("wanderlust", (_) => (l = limit));
    let ex = [];
    C("blade_of_yellow_spring", (amount) => {
      ex = spellcards.map((_, i) => i).splice(-amount);
      hold.splice(-amount);
    });
    C("blood_book", (amount) => (l += amount));
    hold = hold.filter((i) => !useds.includes(i));
    left = left.filter((i) => !hold.includes(i)).rd();
    hold = [...hold, ...left.splice(0, l - hold.length), ...ex];
    left = left.length == 0 ? leftReset() : left;
    useds = [];
    selected = null;
  }
  function leftReset() {
    let res = spellcards.map((_, i) => i);
    C("blade_of_yellow_spring", (amount) => res.splice(-amount));
    return res;
  }
  function C(n, f) {
    if (n in Data.collections) f(Data.collections[n]);
  }
  function S(n, f) {
    if (Data.souvenir == n) f();
    return Data.souvenir == n;
  }
</script>

<div class="spellcards">
  {#each hold as i (i)}
    <div
      class={`spellcard ${
        selected == i && !useThis(i) && "selectedSpellcard"
      } ${useThis(i) && "animate_useSpellcard"}`}
      on:click={(_) => {
        selected = selected == i ? null : i;
        Selected = spellcards[selected];
      }}
    >
      <img src={`/svg/spellcard/${spellcards[i]}.svg`} />
      <txt>{info[spellcards[i]].name}</txt>
      <div>
        <txt>{info[spellcards[i]].detail}</txt>
      </div>
    </div>
  {/each}
</div>

<style>
  .spellcards {
    flex-direction: row;
    align-items: flex-end;
    height: 180px;
  }

  .spellcard {
    width: 120px;
    height: 170px;
    border-radius: 12px;
    border: 4px solid white;
    align-items: center;
    transition: 0.3s;
    filter: drop-shadow(0 0 6px #666);
    cursor: pointer;
    margin: 0px 7px;
    animation: animate_spellcard 0.3s;
  }

  .spellcard:hover {
    filter: drop-shadow(0 0 6px #fff);
  }

  .spellcard > img {
    height: 90px;
    margin-top: 5px;
    padding: 10px 5px;
    transition: 0.3s;
  }

  .spellcard > txt {
    border-top: 2px solid white;
    font-size: 18px;
    color: white;
    font-weight: bold;
    margin-top: 2px;
    width: 100px;
    text-align: center;
    line-height: 45px;
    font-family: SanJiSuXianJianTi;
  }

  .spellcard > div {
    margin-top: 15px;
    align-items: center;
    justify-content: center;
    height: 90px;
    position: absolute;
    top: 0;
  }

  .spellcard > div > txt {
    font-size: 14px;
    color: white;
    width: 84px;
    text-align: center;
    opacity: 0;
    transition: 0.3s;
    font-family: seto;
  }

  .spellcard:hover > img {
    opacity: 0;
  }

  .spellcard:hover > div > txt {
    opacity: 1;
  }

  .selectedSpellcard {
    transform: translateY(-20px);
    z-index: 2;
  }

  .animate_useSpellcard {
    animation: useSpellcard 0.4s forwards;
  }

  @keyframes animate_spellcard {
    0% {
      opacity: 0;
      transform: translateY(120px);
    }

    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }

  @keyframes useSpellcard {
    0% {
      transform: translateY(-20px);
    }
    50% {
      transform: rotateY(90deg) translateY(-20px);
    }

    100% {
      transform: rotateY(90deg) translateY(-20px);
      width: 0;
      margin: 0;
      border: 0;
    }
  }
</style>
