<script>
  import { tweened } from "svelte/motion";
  import { linear } from "svelte/easing";
  import { page, loading } from "../store";
  import words from "../data/words.json";

  const wwords = [
    "东方",
    "幻想",
    "神社",
    "巫女",
    "妖怪",
    "妖精",
    "符卡",
    "结界",
    "彼岸花",
    "魔法使",
    "山麓",
    "提灯",
    "八目鳗",
    "冥界",
    "隙间",
    "楼观剑",
    "八卦炉",
    "祭典",
    "雀酒",
    "月兔",
    "地灵殿",
    "向日葵",
    "枫叶",
    "兽道",
  ];

  let txt1 = random();
  let txt2 = random();
  let txt3 = random();
  let txt4 = random();
  let txt5 = random();
  let txt6 = random();
  let txt7 = random();
  let txt8 = random();
  let txt9 = random();
  let txt0 = random();
  setTimeout((_) => setInterval((_) => (txt1 = random()), 2000), 200 * 0);
  setTimeout((_) => setInterval((_) => (txt2 = random()), 2000), 200 * 1);
  setTimeout((_) => setInterval((_) => (txt3 = random()), 2000), 200 * 2);
  setTimeout((_) => setInterval((_) => (txt4 = random()), 2000), 200 * 3);
  setTimeout((_) => setInterval((_) => (txt5 = random()), 2000), 200 * 4);
  setTimeout((_) => setInterval((_) => (txt6 = random()), 2000), 200 * 5);
  setTimeout((_) => setInterval((_) => (txt7 = random()), 2000), 200 * 6);
  setTimeout((_) => setInterval((_) => (txt8 = random()), 2000), 200 * 7);
  setTimeout((_) => setInterval((_) => (txt9 = random()), 2000), 200 * 8);
  setTimeout((_) => setInterval((_) => (txt0 = random()), 2000), 200 * 9);
  function random() {
    return {
      x: `${-40 - 160 * r()}px`,
      y: `${80 - 160 * r()}px`,
      word: words[Math.floor(r() * words.length)],
    };
  }
  function r() {
    return Math.random();
  }

  let z = 0;
  const opacity = tweened(0, {
    duration: 250,
    easing: linear,
  });

  loading.subscribe((i) => {
    setTimeout((_) => (z = Number(i) - 1), i ? 0 : 200);
    opacity.set(Number(i));
  });
</script>

<div class="body" style="z-index:{z};opacity:{$opacity}">
  <div class="words" style="opacity:{$opacity}">
    {#each [txt1, txt2, txt3, txt4, txt5, txt6, txt7, txt8, txt9, txt0] as p, i (i)}
      <txt
        style="left:{p.x};top:{p.y};font-size:{26 -
          r() * 8}px;animation-delay:{i * 0.2}s"
      >
        {p.word}
      </txt>
    {/each}
  </div>
</div>

<style>
  .body {
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    position: absolute;
    cursor: wait;
    backdrop-filter: blur(80px);
  }

  .words {
    position: relative;
    align-items: center;
    justify-content: center;
  }

  txt {
    color: white;
    font-size: 24px;
    position: absolute;
    width: 240px;
    text-align: center;
    animation: word 2s infinite;
  }

  @keyframes word {
    0% {
      opacity: 0;
    }

    50% {
      opacity: 1;
    }

    100% {
      opacity: 0;
    }
  }
</style>
